export default {
    'pageName': 'recordingform',
    'pageLable': '',
    'student': true,
    'form': {
        'match_name': '',
        'join_date': '',
        'result': '',
        'info': '',
        'student_id': ''
    },
    'formField': [
        {
            'type': 'input',
            'label': '',
            'prop': 'match_name',
            'field': 'match_name'
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': 'join_date',
            'field': 'join_date',
            'datetype': 'date',
        },
        {
            'type': 'input',
            'label': '',
            'prop': 'result',
            'field': 'result'
        },
        {
            'type': 'textarea',
            'label': '',
            'prop': 'info',
            'field': 'info'
        }
    ],
    'pageTable': 'recording',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        match_name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
        join_date: [
            { type: 'date', required: true, message: '', trigger: 'blur' }
        ],
        result: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ]
    }
}


