export default {
    'pageName': 'hourslessonsdetail',
    'pageLable': '',
    'tableSearch': [{
        'localField': 'sclasses_id',
        'from': 'sclasses',
        'foreignField': '_id',
        'as': 'sclasses'
    }, {
        'localField': 'teacher_id',
        'from': 'employee',
        'foreignField': '_id',
        'as': 'employee'
    }, {
        'localField': 'classes_id',
        'from': 'classes',
        'foreignField': '_id',
        'as': 'classes'
    }],
    'pageSearch': [
        {
            'type': 'handleback',
            'searchfunction': function (vm) {
                let filterObj = []
                let classId = vm.$store.state.system.currClassID
                if (classId.length > 0) {
                    filterObj.push({
                        'key': 'classes_id',
                        'value': classId,
                        'type': ''
                    })
                }
                return filterObj
            },
            'fields': [{}]
        },
        {
            'type': 'dateSearch',
            'fields': [{}]
        },
        {
            'type': 'selectSearch',
            'searchfunction': function (form) {
                let filterObj = []
                let searchValue = form + ''
                if (searchValue.length > 0) {
                    filterObj.push({
                        'key': 'classes_id',
                        'value': searchValue,
                        'type': ''
                    })
                }
                return filterObj
            },
            'fields': [
                {
                    'text': '',
                    'showdialog': 'selectclassesdialog',
                    'search': 'class_name'
                }
            ]
        },
        {
            'type': 'singleBtnSearch',
            'span': 5,
            'fields': [
                {
                    'label': '',
                    'type': 'success',
                    'showdialog': 'arrangeform',
                    'actionoption': 'lessonmanageadd'
                }, {
                    'label': '',
                    'type': '',
                    'showdialog': 'lb-cate',
                    'actionoption': 'lessonmanageles'
                },
            ]
        }
    ],
    'pageTableField': [
        {
            'type': 'checkbox',
            'label': '',
        },
        {
            'type': 'operation',
            'label': '',
            'prop': 'setting'
        },
        {
            'type': 'tabletext',
            'label': '',
            'table': 'classes',
            'prop': 'class_name',
        },
        {
            'type': 'tabletext',
            'label': '',
            'table': 'sclasses',
            'prop': 'class_name',
        },
        {
            'type': 'tabletext',
            'label': '',
            'table': 'employee',
            'prop': 'name',
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': 'start',
        },
        {
            'type': 'datetimeRange',
            'label': '',
            'prop1': 'start',
            'prop2': 'end',
        },
        {
            'type': 'checkweek',
            'label': '',
        },
        {
            'type': 'constant',
            'label': '',
            'prop': '',
        }
    ],
    'pageTable': 'coursescheduling',
    'pageTemplate': 'table1',
    'pagePath': ''
}


