<template>
    <div>
        <template v-if="typeData.statutype=='openlessonsstatus'">
            <el-tag type="gray" v-if="getOpen(lessonData,'')"></el-tag>
            <el-tag type="success" v-if="getOpen(lessonData,'open')"></el-tag>
            <el-tag type="danger" v-if="getOpen(lessonData,'close')"></el-tag>
        </template>
        <template v-if="typeData.statutype=='accountcheck'">
            <el-tag type="gray" v-if="lessonData.check_status == '0'"></el-tag>
            <el-tag type="success" v-if="lessonData.check_status == '1'"></el-tag>
        </template>
        <template v-if="typeData.statutype=='checkAccount'">
            <a v-if="lessonData.check_status== '0'" @click="handleCheck(lessonData._id)">
                <el-tag type="gray"></el-tag>
            </a>
            <el-tag type="danger" v-if="lessonData.check_status == '1'"></el-tag>
        </template>
        <template v-if="typeData.statutype=='checkPay'">
            <el-tag type="gray" v-if="lessonData.pay_status==0"></el-tag>
            <el-tag type="success" v-if="lessonData.pay_status==1"></el-tag>
            <el-tag type="danger"  v-if="lessonData.pay_status==2"></el-tag>
        </template>
    </div>
</template>
<script>
export default {
    name: 'LbCheckstatus',
    props: ['lessonData', 'typeData'],
    data() {
        return {
        }
    },
    watch: {
    },
    methods: {
        getOpen(item, value) {
            let opentime = new Date()
            if (value == 'close') {
                return item.status == 2
            } else if (value == 'open') {
                return item.open_time < opentime.getTime()
            } else {
                return item.open_time > opentime.getTime()
            }
        },
        handleCheck(id) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
            }).then(() => {
                this.updateTeble('flow', id, {
                    'check_status': 1
                }).then(() => {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                    this.$emit('search')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            }
                )
        },
    },
    computed: {
    }
}
</script>



