export default {
    'pageName': 'inquiryform',
    'pageLable': '',
    'student': true,
    'form': {
        'track_type': '0',
        'track_way': '',
        'track_time': '',
        'next_time': '',
        'active': false,
        'detail': '',
        'student_id': ''
    },
    'formField': [
        {
            'type': 'radiogroup',
            'label': '',
            'prop': 'track_type',
            'field': 'track_type',
            'labels': [
                { 'label': '' },
                { 'label': '' }
            ]
        },
        {
            'type': 'inputselect',
            'label': '',
            'prop': 'track_way',
            'field': 'track_way',
            data(vm) {
                return vm.getDictData('4')
            }
        },
        {
            'type': 'input',
            'label': '',
            'prop': 'op_name',
            'field': 'op_name'
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': 'track_time',
            'field': 'track_time',
            'datetype': 'datetime',
        },
        {
            'type': 'textarea',
            'label': '',
            'prop': '',
            'field': 'detail'
        },
        {
            'type': 'switchdatetime',
            'label': '',
            'prop': '',
            'field': 'next_time',
            'fieldActive': 'active',
            'datetype': 'datetime',
        }
    ],
    'pageTable': 'inquiry',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        track_way: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
        detail: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
        op_name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
        track_time: [
            { type: 'date', required: true, message: '', trigger: 'blur' }
        ],
    }
}


