export default {
    'pageName': 'employeeform',
    'pageLable': '',
    'form': {
        'name': '',
        'sex': '0',
        'roles_id': [],
        'is_part_time': '0',
        'tel': '',
        'email': '',
        'lock': false,
        'birth': ''
    },
    'formField': [
        {
            'type': 'input',
            'label': '',
            'prop': 'name',
            'field': 'name'
        },
        {
            'type': 'input',
            'label': '()',
            'prop': 'tel',
            'field': 'tel'
        },
        {
            'type': 'input',
            'label': 'Email',
            'prop': '',
            'field': 'email'
        },
        {
            'type': 'option',
            'label': '',
            'prop': '',
            'field': 'roles_id',
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': '',
            'field': 'birth',
            'datetype': 'date',
        },
        {
            'type': 'radiogroup',
            'label': '',
            'prop': '',
            'field': 'sex',
            'labels': [
                { 'label': '' },
                { 'label': '' }
            ]
        },
        {
            'type': 'radiogroup',
            'label': '',
            'prop': '',
            'field': 'is_part_time',
            'labels': [
                { 'label': '' },
                { 'label': '' }
            ]
        }
    ],
    'pageTable': 'employee',
    'pageTemplate': 'form',
    'pagePath': '',
    mounted(vm) {
        vm.getTableApidata('role')
    },
    rulesData(vm) {
        return {
            name: [
                { required: true, message: '', trigger: 'blur' },
                { min: 1, max: 256, message: ' 1  256', trigger: 'blur' }
            ],
            tel: [
                { validator: vm.validateTel, required: true, trigger: 'blur' }
            ],
        }
    }
}


