export default {
    'pageName': 'sclassesform',
    'pageLable': '',
    'form': {
        'class_name': '',
        'max_student_num': 0
    },
    'formField': [
        {
            'type': 'input',
            'label': '',
            'prop': 'class_name',
            'field': 'class_name'
        },
        {
            'type': 'numberinput',
            'label': '',
            'text': '',
            'prop': '',
            'field': 'max_student_num'
        }
    ],
    'pageTable': 'sclasses',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        class_name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
    }
}


