<template>
    <div class="=wrapper">
        <div class="wrapper-xs">
            <div class="wrapper panel panel-default bg-white">
                <div class="panel panel-default" style="margin-top:14px;">
                    <div class="panel-heading"></div>
                    <ul class="list-group">
    
                        <li class="list-group-item clear" v-for="(item, index) in localdata.dict">
                            <span class="pull-right">
                                <template v-if="getActionOption('systememdictedit')">
                                    <a @click="handleShowDialog('lb-dictitems',item)"></a>
                                </template>
                            </span>
                            <h4 class="list-group-item-head text-danger">{{item.text}}</h4>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'dict',
    data() {
        let localdata = {
            'dict': [{
                'type': '1',
                'text': ''
            }, {
                'type': '2',
                'text': ''
            }, {
                'type': '3',
                'text': ''
            }, {
                'type': '4',
                'text': ''
            }, {
                'type': '5',
                'text': ''
            }, {
                'type': '6',
                'text': ''
            }, {
                'type': '7',
                'text': ''
            }, {
                'type': '8',
                'text': ''
            },]
        }
        return {
            localdata,
        }
    },
    computed: {},
    watch: {},
    methods: {}
}
</script>



