export default {
    'pageName': 'campusform',
    'pageLable': '',
    'form': {
        'group_name': '',
        'branch_name': '',
        'short_name': '',
        'branch_tel': '',
        'city': '',
        'branch_address': ''
    },
    'formField': [
        {
            'type': 'input',
            'label': '',
            'prop': 'branch_name',
            'field': 'branch_name'
        },
        {
            'type': 'input',
            'label': '',
            'prop': '',
            'field': 'short_name'
        },
        {
            'type': 'input',
            'label': '',
            'prop': '',
            'field': 'branch_tel'
        },
        {
            'type': 'input',
            'label': '',
            'prop': '',
            'field': 'group_name'
        },
        {
            'type': 'input',
            'label': '',
            'prop': '',
            'field': 'address'
        }
    ],
    'pageTable': 'campus',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        branch_name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
    }
}


