export default {
    'pageName': 'lessonorders',
    'pageLable': '',
    'tableSearch': [{
        'localField': 'student_id',
        'from': 'student',
        'foreignField': '_id',
        'as': 'student'
    }],
    'pageSearch': [
        {
            'type': 'textSearch',
            'fields': [
                {
                    'label': '',
                    'defvalue': '',
                    'default': true,
                    'value': 'order_no'
                }
            ]
        },
        {
            'type': 'selectSearch',
            'searchfunction': function (form) {
                let filterObj = []
                let searchValue = form + ''
                if (searchValue.length > 0) {
                    filterObj.push({
                    'key': 'student_id',
                    'value': searchValue,
                    'type': ''
                })
                }
                return filterObj
            },
            'fields': [
                {
                    'text': '',
                    'showdialog': 'selectstudentdialog',
                    'search': 'student_name'
                }
            ]
        },
        {
            'type': 'radioGroupSearch',
            'searchfunction': function (form) {
                let filterObj = []
                let search_value = form+''
                if (search_value.length>0) {
                    filterObj.push({
                        'key': 'pay_status',
                        'value': Number(search_value),
                        'type': ''
                    })
                }
                return filterObj
            },
            'fields': [
                {
                    'labels': [
                        { 'label': '' },
                        { 'label': '' },
                        { 'label': '' },
                    ]
                }
            ]
        }
    ],
    'pageTableField': [
        {
            'type': 'operation',
            'label': '',
        },
        {
            'type': 'tabletext',
            'label': '',
            'table': 'student',
            'prop': 'student_name',
        },
        {
            'type': 'getButtongroupText',
            'label': '',
            'color': 'gray',
            'prop': 'order_type',
            'othertype': [{
                'value': 1,
                'text': ''
            }, {
                'value': 2,
                'text': ''
            }, {
                'value': 3,
                'text': ''
            }],
        },
        {
            'type': 'text',
            'label': '',
            'prop': 'order_no',
        },
        {
            'type': 'getToFixed',
            'label': '',
            'prop': 'origin_amount',
        },
        {
            'type': 'text',
            'label': '',
            'prop': 'body',
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': 'creattime',
        },
        {
            'type': 'getButtongroupText',
            'label': '',
            'color': 'gray',
            'prop': 'pay_status',
            'othertype': [{
                'value': -1,
                'text': ''
            }, {
                'value': 0,
                'text': ''
            }, {
                'value': 1,
                'text': ''
            }, {
                'value': 2,
                'text': ''
            }]
        },
        {
            'type': 'text',
            'label': '',
            'prop': 'order_remark',
        },
    ],
    'pageTable': 'order',
    'pageTemplate': 'table1',
    'pagePath': ''
}


