export default {
    'pageName': 'classesform',
    'pageLable': '',
    'form': {
        'class_name': '',
        'course_id': '',
        'open_time': '',
        'close_time': '',
        'max_student_num': 0,
        'total_times': 0,
        'unit_hours': 0,
        'teacher_id': '',
        'del_able': false,
        'class_flag': 0
    },
    'formField': [
        {
            'type': 'selectSearch',
            'label': '',
            'prop': 'teacher_id',
            'field': 'teacher_id',
            'text': '',
            'showdialog': 'selectteacherdialog',
            'search': 'name',
            'table':'employee'
        },
        {
            'type': 'selectSearch',
            'label': '',
            'prop': 'course_id',
            'field': 'course_id',
            'text': '',
            'showdialog': 'selectlessondialog',
            'search': 'lesson_name',
            'table':'course'
        },
        {
            'type': 'input',
            'label': '',
            'prop': 'class_name',
            'field': 'class_name'
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': '',
            'field': 'open_time',
            'datetype': 'date',
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': '',
            'field': 'close_time',
            'datetype': 'date',
        },
        {
            'type': 'numberinput',
            'label': '',
            'prop': '',
            'field': 'max_student_num',
            'text': ''
        },
        {
            'type': 'numberinput',
            'label': '',
            'prop': '',
            'field': 'total_times',
            'text': ''
        },
        {
            'type': 'numberinput',
            'label': '',
            'prop': '',
            'field': 'unit_hours',
            'text': ''
        }
    ],
    'pageTable': 'classes',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        teacher_id: [
            { required: true, message: '', trigger: 'blur' }
        ],
        course_id: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
        class_name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ]
    }
}



