export default {
    'pageName': 'studenttracks',
    'pageLable': '',
    'tableSearch': [
        {
            'localField': 'student_id',
            'from': 'student',
            'foreignField': '_id',
            'as': 'student'
        }
    ],
    'pageSearch': [
        {
            'type': 'dateSearch',
            'searchfunction': function (form, vm) {
                let filterObj = []
                if (form && form.length == 2) {
                    let startTime = vm.getDatetime(form[0])
                    let endTime = vm.getDatetime(form[1])
                    if (startTime > 0) {
                        if (startTime == endTime) {
                            endTime = vm.getDatetimeEndOf(form[1])
                        }
                        filterObj.push({
                            'key': 'track_time',
                            'value': startTime,
                            'type': 'gte'
                        })
                        filterObj.push({
                            'key': 'track_time',
                            'value': endTime,
                            'type': 'lte'
                        })
                    }
                }
                return filterObj
            },
            'fields': [{}]
        },
        {
            'type': 'radioGroupSearch',
            'searchfunction': function (form) {
                console.log('js',form)
                let filterObj = []
                let status = form+''
                if (status.length>0) {
                    filterObj.push({
                        'key': 'track_type',
                        'value': status,
                        'type': ''
                    })
                }
                console.log(filterObj)
                return filterObj
            },
            'fields': [
                {
                    'labels': [
                        { 'label': '' },
                        { 'label': '' }
                    ]
                }
            ]
        },
        {
            'type': 'selectSearch',
            'searchfunction': function (form) {
                let filterObj = []
                let searchValue = form + ''
                if (searchValue.length > 0) {
                    filterObj.push({
                    'key': 'student_id',
                    'value': searchValue,
                    'type': ''
                })
                }
                return filterObj
            },
            'fields': [
                {
                    'text': '',
                    'showdialog': 'selectstudentdialog',
                    'search': 'student_name'
                }
            ]
        },
        {
            'type': 'singleBtnSearch',
            'fields': [
                {
                    'label': '',
                    'type': 'success',
                    'icon': 'edit',
                    'showdialog': 'inquiryform',
                    'actionoption': 'studenttracksadd'
                }
            ]
        },
    ],
    'pageTableField': [
        {
            'type': 'studenttracksadd',
            'label': '',
            'prop': 'student',
        },
        {
            'type': 'constant',
            'label': '',
            'prop': '',
        },
        {
            'type': 'text',
            'label': '',
            'prop': 'detail',
        },
        {
            'type': 'text',
            'label': '',
            'prop': 'track_way',
        },
        {
            'type': 'text',
            'label': '',
            'prop': 'op_name',
        },
        {
            'type': 'datetime',
            'label': '',
            'prop': 'track_time'
        },
        {
            'type': 'getButtongroupText',
            'label': '',
            'color': 'gray',
            'prop': 'track_type',
            'othertype': [{
                'value': '0',
                'text': ''
            }, {
                'value': '1',
                'text': ''
            }]
        }
    ],
    'pageTable': 'inquiry',
    'pageTemplate': 'table1',
    'pagePath': ''
}


