export default {
    menuTitle: '',
    menuIcon: 'fa-book',
    menu: [{
        menuTitle: '',
        to: '/lesson/sclasses',
        menuIcon: 'fa fa-certificate',
        dropDownMenu: [{
            'url': 'sclassesform',
            'icon': 'fa fa-pencil',
            'text': ''
        }, {
            'action': 'delete',
            'icon': 'fa fa-times',
            'text': ''
        }],
        action: [{
            'text': '',
            'action': 'lessonsclasses'
        }]
    }, {
        menuTitle: '',
        to: '/lesson/manage',
        menuIcon: 'fa fa-gears',
        dropDownMenu: [{
            'url': 'lb-newlessonmodal',
            'icon': 'fa fa-pencil',
            'text': ''
        }, {
            'action': 'delete',
            'icon': 'fa fa-times',
            'text': ''
        }],
        action: [{
            'text': '',
            'action': 'lessonmanageadd'
        }, {
            'text': '',
            'action': 'lessonmanageles'
        }]
    }, {
        menuTitle: '',
        to: '/lesson/classes',
        menuIcon: 'fa fa-gear',
        dropDownMenu: [{
            'url': 'classesform',
            'icon': 'fa fa-pencil',
            'text': ''
        }, {
            'action': 'delete',
            'icon': 'fa fa-times',
            'text': ''
        }],
        action: [{
            'text': '',
            'action': 'lessonclasses'
        }]
    }, {
        menuTitle: '',
        to: '/lesson/orders',
        menuIcon: 'fa fa-envelope-o',
        dropDownMenu: [{
            'url': 'lb-refunds',
            'icon': 'fa fa-reply',
            'text': '',
            'menuctrl': 'pay_status',
            'menuvalue': [1, 2]
        }, {
            'action': 'delete',
            'icon': 'fa fa-times',
            'text': '',
            'menuctrl': 'pay_status',
            'menuvalue': 0
        }, {
            'url': 'lb-printerorder',
            'icon': 'fa fa-print',
            'text': '',
        }],
    }]
}


